@import url(./reset.css);
@import url(./grid.css);
@import url("https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,400i,700");
@import url("https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css");
@import url("https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css");
body, html {
  margin: 0;
  padding: 0; }

body {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fefefe; }

h3 {
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 700; }

h4 {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 700;
  color: #333; }

h5 {
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 700;
  color: #333; }

p {
  font-size: 14px;
  line-height: 24px;
  color: #666; }

a {
  text-decoration: none;
  color: #F1C40F; }
  a:hover {
    color: #CAA40D; }

img {
  display: block;
  width: 100%; }

hr {
  max-width: 320px;
  height: 1px;
  margin: 50px auto;
