  font-size: 12px;
  font-style: italic;
  font-weight: 300;
  text-transform: uppercase;
  color: #fff; }

.nav {
  display: block; }

.nav-link {
  display: inline-block;
  margin-left: 20px;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
  color: #fff; }

label {
  font-size: 18px;
  line-height: 24px;
  color: #fff; }

.select-wrapper {
  border: 1px solid #d3dbe2;
  border-radius: 3px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  background: #fff;
  overflow: hidden;
  vertical-align: bottom; }
  .select-wrapper select {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    padding: 10px 15px;
    padding-right: 32px;
    border: 0;
    width: 100%;
