    -moz-appearance: none;
    cursor: pointer; }
    .select-wrapper select:focus {
      outline: 0; }
  .select-wrapper .select-icon {
    cursor: pointer;
    display: block;
    fill: #798c9c;
    position: absolute;
    right: 10px;
    top: 50%;
