    width: 12px;
    height: 12px;
    vertical-align: middle; }

.product-price {
  font-size: 24px;
  line-height: 40px;
  color: #fff; }

.product-image {
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  overflow: hidden; }

.shopify-secure-badge {
  display: inline-block; }
  .shopify-secure-badge img {
    display: block; }

.shopify-secure-text {
  display: inline-block;
  vertical-align: top;
  width: 200px;
  padding: 4px 10px;
  font-size: 12px;
